<script>
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
// import { getRoutePlainFare, getDescAllInfo } from '../../api/api'
export default {
	data() {
		return {
			themobile: '',
			thewechat: '',
			cname: '',
			ename: '',
			passport: '',
 			customers: 6,
			status: '',
			nationality: '',
			validity: '',
			name: '',
			issueplace: '',
			mobile: '',
			showmask: false,
			wechat: '',
			paystatus: '',
			issubmit: true,
			agreeit: false,
			showmask2: false,
			showmask3: false,
			temnum: ''
		}
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer
	},
	created() {
		if (this.getCookie('login_type') !== '') {
			this.status = 3;
		} else {
			let cookie = sessionStorage.getItem(this.encryption('info'));
			if (!cookie) {
				window.location.href = 'product.html';
			} else {
				this.status = 3;
			}
		}
	},
	mounted() {
		this.resetHeight();
		window.onresize = () => {
			this.resetHeight();
		}
	},
	watch: {
		showmask() {
			if (this.showmask === true) {
				window.scroll(0, 0);
				document.body.style.overflowX = 'hidden';
			} else {
			}
		},
		showmask2() {
			if (this.showmask2 === true) {
				window.scroll(0, 0);
				document.body.style.overflowX = 'hidden';
			} else {
			}
		}
	},
	methods: {
		getCookie(name) {
      if (document.cookie.length > 0) {
        var start = document.cookie.indexOf(name + '=');
        if (start !== -1) {
        start = start + name.length + 1
        var end = document.cookie.indexOf(';', start);
        if (end === -1) end = document.cookie.length
        return unescape(document.cookie.substring(start, end))
        }
        }
        return ''
    },
		saveinfo() {
			if (this.cname === '') {
				this.$message('请输入中文姓名');
			} else if (this.ename === '') {
				this.$message('请输入英文姓名');
			} else if (this.passport === '') {
				this.$message('请输入护照号');
			} else if (this.validity === '') {
				this.$message('请输入护照有效期');
			} else if (this.nationality === '') {
				this.$message('请输入国籍');
			} else if (this.issueplace === '') {
				this.$message('请输入签发地点');
			} else if (this.themobile !== '' && !(/^1[34578]\d{9}$/.test(this.themobile))) {
				this.$message('请输入正确的手机号');
			} else {
				alert('ok');
			}
		},
		modifyconfirm() {
			if (this.name === '') {
				this.$message('请输入姓名');
			} else if (this.mobile === '') {
				this.$message('请输入手机号');
			} else if (!(/^1[34578]\d{9}$/.test(this.mobile))) {
				this.$message('请填写正确的手机号');
			} else {
			}
		},
		modifyone() {
			this.showmask3 = true;
		},
		handleCurrentChange(val) {
		},
		use() {
			this.showmask = true;
			this.showmask2 = false;
			this.cname = '哈哈';
			this.ename = 'Haha';
		},
		resetHeight() {
				document.getElementById('mask').style.height = document.body.scrollHeight + 'px';
		},
		open2() {
			this.showmask = false;
			this.showmask2 = true;
		},
		openbox(n, index) {
			this.temnum = n;
			this.showmask = true;
		},
		hidemask() {
			this.showmask = false;
			this.showmask2 = false;
			this.showmask3 = false;
		},
		backtwo() {
			this.issubmit = false;
		},
		refresh() {
			window.location.href = 'paystep.html';
		},
		agreeinset() {
			alert('弹窗');
		},
		chooseagree() {
			this.agreeit = true;
		},
		// 第一步确认按钮
		gotwo() {
			if (this.name === '') {
				this.$message('请输入姓名');
			} else if (this.mobile === '') {
				this.$message('请输入手机号');
			} else if (!(/^1[34578]\d{9}$/.test(this.mobile))) {
				this.$message('请填写正确的手机号');
			} else {
				this.status = 2;
			}
		},
		// 第二步确认按钮
		confirmtwo() {
			if (this.paystatus === '') {
				this.$message('请选择支付方式');
			} else if (this.agreeit === false) {
				this.$message('请阅读协议内容');
			} else {
				this.issubmit = true;
			}
		},
		chooseone() {
			this.paystatus = 1;
		},
		choosetwo() {
			this.paystatus = 2;
		}
	}
}
</script>

